
import Vue from 'vue'
import { mapGetters } from 'vuex'
export default Vue.extend({
  computed: {
    ...mapGetters(['currentUser', 'userPosts'])
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.go(-1)
    }
    this.$store.dispatch('getPostsByUserId', this.$route.params.userId)
  }
})
